import { EventType } from './constants';

export interface Tag {
  epochId?: number;
  roundId?: number;
  stepName?: string;
  [key: string]: null | string | number | undefined;
}

export enum Keyframe {
  START_EPOCH,
  START_ROUND,
  START_STEP,
}

export type KeyframeKey = Keyframe;

export interface BaseEvent {
  timestamp: number;
  eventName: string;
  tag?: Tag;
}

export interface KeyframeEvent extends BaseEvent {
  keyframe: Keyframe;
}

export interface EpochKeyframe extends BaseEvent {
  keyframe: Keyframe.START_EPOCH;
  tag: Tag & { epochId: number };
}

export interface RoundKeyframe extends BaseEvent {
  keyframe: Keyframe.START_ROUND;
  tag: Tag & { roundId: number };
}

export interface StepKeyframe extends BaseEvent {
  keyframe: Keyframe.START_STEP;
  tag: Tag & { stepName: string };
}

export interface ProposeEvent extends BaseEvent {
  eventType: EventType.Propose;
  tag: Tag & {
    epochId: number;
    roundId: number;
    proposer: string;
    hash: string;
  };
}

export interface VoteEvent extends BaseEvent {
  eventType: EventType.Vote;
  tag: Tag & {
    epochId: number;
    roundId: number;
    voter: string;
    hash: string;
  };
}

export type TraceEvent =
  | BaseEvent
  | EpochKeyframe
  | RoundKeyframe
  | StepKeyframe
  | ProposeEvent
  | VoteEvent;
