import React from 'react';
import { Card, Row, Col, Table } from 'antd';
import styled from 'styled-components';
import { useStoreState } from '../store';
import { isVoteEvent, Slice } from '../event';
import _ from 'lodash';
import { ProposeEvent, VoteEvent } from '../types';
import { isProposeEvent } from '../event';
import { ColumnProps } from 'antd/es/table';

function shortHex(hex: string) {
  return hex.slice(0, 8);
}

const DashboardWrapper = styled.div`
  padding: 16px 0;
`;

function ProposeTable(props: { slice: Slice }) {
  const events = props.slice.events();

  const proposeEvents = events.filter<ProposeEvent>(isProposeEvent);

  const columns: ColumnProps<ProposeEvent>[] = [
    { title: 'Name', key: 'eventName', dataIndex: 'eventName' },
    { title: 'Round', key: 'tag.roundId', dataIndex: 'tag.roundId' },
    {
      title: 'Proposer',
      key: 'tag.proposer',
      dataIndex: 'tag.proposer',
      render: shortHex,
    },
    {
      title: 'Hash',
      key: 'tag.hash',
      dataIndex: 'tag.hash',
      render: shortHex,
    },
  ];
  return (
    <Table
      size="small"
      pagination={false}
      scroll={{ y: 180 }}
      columns={columns}
      dataSource={proposeEvents}
    />
  );
}

function VoteTable(props: { slice: Slice }) {
  const events = props.slice.events();

  const voteEvents = events.filter<VoteEvent>(isVoteEvent);

  const columns: ColumnProps<VoteEvent>[] = [
    { title: 'Name', key: 'eventName', dataIndex: 'eventName' },
    { title: 'Round', key: 'tag.roundId', dataIndex: 'tag.roundId' },
    {
      title: 'Voter',
      key: 'tag.voter',
      dataIndex: 'tag.voter',
      render: shortHex,
    },
    {
      title: 'Hash',
      key: 'tag.hash',
      dataIndex: 'tag.hash',
      render: shortHex,
    },
  ];
  return (
    <Table
      size="small"
      pagination={false}
      scroll={{ y: 180 }}
      columns={columns}
      dataSource={voteEvents}
    />
  );
}

export const Dashboard = () => {
  const { activeEpoch, activeRound, epochSlice, roundSlice } = useStoreState(
    state => state.tracer,
  );

  const slice: Slice = _.isNil(activeRound) ? epochSlice : roundSlice;

  return (
    <DashboardWrapper>
      <Row gutter={16}>
        <Col md={12} sm={24}>
          <Card title="Received proposal">
            <ProposeTable slice={slice} />
          </Card>
        </Col>
        <Col md={12} sm={24}>
          <Card title="Received vote">
            <VoteTable slice={slice} />
          </Card>
        </Col>
      </Row>
    </DashboardWrapper>
  );
};
